<script>
    import { afterUpdate, onDestroy, onMount } from "svelte";
    import timeDiff from 'timediff'

    export let item;

    let timeRemaining = '';
    let updateTimeInterval = null;
    let finishTimer = null;
    let color = 'rgb(255,255,255,0.75)';
    let timeDifference = null;

    $: if (item.deadline) {
        updateDisplay();
    }
    
    function updateDisplay() {
        timeDifference = timeDiff(new Date(), new Date(item.deadline),{
            units: 'mHDWY',
            returnZeros: false
        });
        
        timeRemaining = '';
        Object.entries(timeDifference).forEach(value => {
            timeRemaining += value[1] + value[0][0] + ' ';
        });

        let secondsRemaining = (new Date(item.deadline) - new Date()) / 1000 ;
        let minutesRemaining = secondsRemaining / 60;
        let hoursRemaining = minutesRemaining / 60;

        color = 'rgb(255,255,255,0.75)';
        if(hoursRemaining < 1) color = 'yellow';
        if(minutesRemaining < 10) color = 'orange';
        if(minutesRemaining < 5 ) color = 'red';

        if(minutesRemaining < 1) timeRemaining = 'Now!';
    }

    onMount(() => {
        updateDisplay();
        updateTimeInterval = setInterval(()=>{ updateDisplay(); }, 1000 * 5);
        if(finishTimer) clearTimeout(finishTimer);

    });

    onDestroy(() => {
        if(updateTimeInterval) clearInterval(updateTimeInterval);
    });


    afterUpdate(() => {
        if(finishTimer) clearTimeout(finishTimer);

        let secondsUntilDeadline = (new Date(item.deadline) - new Date());
        if(secondsUntilDeadline <= 1) return;

        finishTimer = setTimeout(() => {
            console.log('Finished!');
            let not = new Notification(item.text);
        }, secondsUntilDeadline);
    });

</script>

<p style='color:{color}'>{timeRemaining}</p>

<style>
    p {
        position:absolute;
        right:1.5em;
        margin-left: auto;
        font-size: 1em;
        font-weight:lighter;
        padding-right: 1em;
        text-shadow: 1px 1px rgba(0, 0, 0, 1);
    }

    p:hover {
        color: rgb(255,255,255,1.0) !important;
    }

</style>

