<script>
    import ToDoItem from './ToDoItem.svelte'
    export let todos;
    let finalList = [];
    $: {
        if(todos) { finalList = sortList();} 
    }

    function sortList() {
        let all = Object.values(todos);
        let sorted = all.sort((a,b) => {
            let aTime = new Date(a.deadline).getTime();
            let bTime = new Date(b.deadline).getTime();
            if(aTime < bTime) return -1;
            if(aTime > bTime) return 1;
            return 0;
        });
        return sorted;
    }
</script>


<div id='todos'>
    {#each finalList as todo (todo.id)}
        <ToDoItem item={todo} on:complete on:updateDeadline/>
    {/each}

</div>

<style>
    #todos {
        border: solid 1px rgba(0, 0, 0, 0.2);
        display: flex;
        flex-direction: column;
        overflow-y:auto;
        padding:1em;
    }
</style>
